.loader {
  height: 150px;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 15px;
  height: 15px;
  color: #000;
  background: currentColor;
  border-radius: 50%;
  margin: 10px auto;
  animation: left 1s ease-in-out infinite;
  display: block;
  position: relative;
  box-shadow: 0 50px, 0 100px;
}

.loader:after {
  animation: right 1.1s ease-in-out infinite;
}

@keyframes right {
  0%, 100% {
    transform: translate(-15px);
  }

  50% {
    transform: translate(15px);
  }
}

@keyframes left {
  0%, 100% {
    transform: translate(15px);
  }

  50% {
    transform: translate(-15px);
  }
}

